import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { get_comments, publish_comment } from "../../redux/actions/products";
import Comment from "../Comments/Comment";

const mapState = (state) => ({
	isAuthenticated: state.auth.isAuthenticated,
	user: state.auth.user,
});

function Tabs({ product }) {
	const { t } = useTranslation(["product"]);
	const dispatch = useDispatch();
	const [choosedTab, setChoosedTab] = useState("description");
	const [comments, setComments] = useState(null);
	const { isAuthenticated, user } = useSelector(mapState);
	const [loadMore, setLoadMore] = useState(null);
	const [result, setResult] = useState(null);
	const [commentsResults, setCommentsResults] = useState(null);
	const [bodyInput, setBodyInput] = useState(null);

	const handleLoadMore = () => {
		setLoadMore(commentsResults.length);
		getCommentsHandle();
	};
	const handleChangeInput = (e) => {
		setBodyInput(e.target.value);
	};

	const handlePublish = (e) => {
		e.preventDefault();
		if (!bodyInput || bodyInput.length < 1) {
			setResult("Please write somthing!");
			return setTimeout(() => {
				setResult(null);
			}, 3000);
		}
		dispatch(publish_comment(bodyInput, product.id)).then((resultt) => {
			if (resultt === 201) {
				setResult("Created!");
				setBodyInput("");
				getCommentsHandle();
			} else {
				setResult("Error!");
				setBodyInput("");
			}
			setTimeout(() => {
				setResult(null);
			}, 10000);
		});
	};
	const getCommentsHandle = () => {
		const confi = `limit=10${loadMore ? `&offset=${loadMore}` : ""}`;
		dispatch(get_comments(product?.id, confi)).then((result) => {
			if (loadMore) {
				setComments(result);
				setCommentsResults([...commentsResults, ...result.results]);
			} else {
				if (result) {
					setComments(result);
					setCommentsResults(result.results);
				}
			}
		});
	};
	useEffect(() => {
		getCommentsHandle();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [product]);
	return (
		<div className="my-4 ">
			{/* Tabs Name */}
			<ul class="nav nav-pills mb-3">
				{/* 1 */}
				<li key={"description"} class="nav-item" role="presentation">
					<button
						class={`btn ${choosedTab === "description" ? "pp-active" : "pp"}`}
						type="button"
						onClick={() => setChoosedTab("description")}
						role="tab"
					>
						{t("product:description")}
					</button>
				</li>
				{/* 2 */}
				<li key={"comments"} class="nav-item" role="presentation">
					<button
						class={`btn ${choosedTab === "comments" ? "pp-active" : "pp"}`}
						type="button"
						onClick={() => setChoosedTab("comments")}
						role="tab"
					>
						{comments?.count} {t("product:Comments")}
					</button>
				</li>
				{/* 3 */}
				<li key={"add-comment"} class="nav-item" role="presentation">
					<button
						class={`btn ${choosedTab === "add-comment" ? "pp-active" : "pp"}`}
						type="button"
						onClick={() => setChoosedTab("add-comment")}
						role="tab"
					>
						{t("product:addComment")}
					</button>
				</li>
			</ul>
			{/* Tabs Content */}
			<div>
				{/* 1 */}
				<div role="tabpanel">
					{choosedTab === "description" && product?.description}
				</div>
				{/* 2 */}
				{choosedTab === "comments" && product && (
					<div role="tabpanel">
						<div className="row">
							{comments &&
							Array.isArray(commentsResults) &&
							commentsResults.length >= 1 ? (
								commentsResults.map((comment) => {
									return (
										<div className="col-md-6">
											<Comment
												comment={comment}
												imgSrc={product?.picture_search}
											/>
										</div>
									);
								})
							) : (
								<h1 className="text-center py-5"> {t("product:noComments")}</h1>
							)}
						</div>
						{comments?.next && (
							<button
								className="btn btn-primary"
								onClick={() => handleLoadMore()}
							>
								{t("product:loadMore")}
							</button>
						)}

						{/* Products End */}
					</div>
				)}
				{/* 3 */}
				<div role="tabpanel">
					{choosedTab === "add-comment" &&
						(isAuthenticated ? (
							<div className="row">
								<form action="#">
									<h3 className="mb-3"> {t("product:addComment")}</h3>

									<div className="d-flex my-2">
										<div className="col-auto">
											<img
												alt="Profile"
												src={user?.profile_avatar}
												style={{
													maxHeight: "50px",
													maxWidth: "60px",
													borderRadius: "10px",
												}}
											/>
										</div>
										<div
											class="input-group col-auto position-relative ms-3 "
											style={{ borderRadius: "0.75em", maxWidth: "250px" }}
										>
											{/* Input */}
											<input
												type="text"
												className="form-control border-0"
												aria-label="Search"
												placeholder="Search..."
												value={user?.username}
												readOnly
												style={{ borderRadius: "0.75em" }}
											/>
										</div>
									</div>
									<div className="mt-4">
										<label
											className="form-label"
											for="exampleFormControlTextarea1"
										>
											{t("product:Body")}
										</label>
										<div className="">
											<textarea
												style={{ minWidth: "100%" }}
												className="form-control"
												value={bodyInput}
												onChange={(e) => handleChangeInput(e)}
												placeholder={t("product:addComment_desc")}
												// placeholder="What do you want to say about product"
												id="exampleFormControlTextarea1"
												rows="6"
											></textarea>
										</div>

										<div>
											{result && result === "Created!" && (
												<button
													className="btn btn-success mt-3"
													type="submit"
													disabled={true}
													onClick={(e) => handlePublish(e)}
												>
													{t("product:createdComment")}
													<i class="bi bi-cloud-arrow-up-fill ms-2"></i>
												</button>
											)}
											{(result && result === "Error!") ||
												(result === "Please write somthing!" && (
													<button
														className="btn btn-danger mt-3"
														type="submit"
														disabled={true}
														onClick={(e) => handlePublish(e)}
													>
														{t("product:commentError")}
														<i class="bi bi-cloud-arrow-up-fill ms-2"></i>
													</button>
												))}
											{!result && (
												<button
													className="btn btn-outline-primary mt-3"
													type="submit"
													disabled={!bodyInput}
													onClick={(e) => handlePublish(e)}
												>
													{t("product:publishComment")}
													<i class="bi bi-cloud-arrow-up-fill ms-2"></i>
												</button>
											)}
										</div>
									</div>
								</form>
							</div>
						) : (
							<div className="d-flex flex-column justify-content-center my-5">
								<h1 className="text-dark pb-3 text-center">
									{/* Login to add a comment */}
									{t("product:login_to_addComment")}
								</h1>
								<Link
									to="/login"
									className="btn btn-primary mx-auto"
									style={{ maxWidth: "350px", minWidth: "150px" }}
								>
									{t("product:login")}
								</Link>
							</div>
						))}
				</div>
			</div>
		</div>
	);
}

export default Tabs;
