import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams, Link } from "react-router-dom";
import { get_product, get_sizes } from "./../redux/actions/products";
import { selectCartItems } from "./../redux/selectors/cart";
import { addProduct, reduceCartItem } from "./../redux/actions/cart";
import { ADD_TO_WISHLIST } from "../redux/types/types";
import { selectWishItems } from "../redux/selectors/wishlist";
import { useTranslation } from "react-i18next";
import Recommendation from "../components/Product/Recommendation";
import Tabs from "../components/Product/Tabs";
import Breadcumb from "../components/Products/Breadcumb";
import { Helmet } from "react-helmet";

const mapState = (state) => ({
	items: selectCartItems(state),
	wishItems: selectWishItems(state),
});

function Product() {
	const { t, i18n } = useTranslation(["product"]);
	const { id } = useParams();
	const { items, wishItems } = useSelector(mapState);
	const [product, setProduct] = useState(null);
	const [sizes, setSizes] = useState([]);

	const [checkWishItem, setWishCheckItem] = useState(null);
	const dispatch = useDispatch();

	useEffect(() => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
		dispatch(get_product(id, i18n.language)).then((result) => {
			setProduct(result);
			dispatch(get_sizes(id, i18n.language)).then((res) => {
				setSizes(res);
			});
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [id, i18n.language]);

	// TODO: USe here useMemo or useCallback
	useEffect(() => {
		setWishCheckItem(wishItems.find((wishItem) => wishItem.id === product?.id));
	}, [wishItems, product]);

	return (
		<div className="py-5 container">
			<Breadcumb
				topmar={true}
				searchedCategoryName={
					<>
						<Link
							to={`/category/${product?.category.slug}/`}
							className="text-decoration-none text-muted ps-1"
						>
							{product?.category.name}
						</Link>{" "}
						\ {product?.name}
					</>
				}
			/>
			<Helmet>
				<title>{product?.name}</title>
				<meta name="description" content={product?.description.slice(0, 100)} />
			</Helmet>

			{/* Product Info Start */}
			<div className="row  mt-5">
				{/* Product Image */}
				<div className=" col-md-7">
					<img
						alt="Product"
						src={product?.picture_middle}
						style={{ borderRadius: "30px" }}
						className="img-fluid"
					/>
				</div>
				{/* Product Desc */}
				<div
					className=" bg-white position-relative my-3 mx-auto w-95 col-md-5 font-weight-bolder d-flex flex-column p-5"
					style={{
						borderRadius: "30px",
						// width: "95%",
						boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
					}}
				>
					<button
						className={`badge btn bg-transparent like-btn ${
							checkWishItem ? "text-danger" : "text-grayi"
						}  position-absolute`}
						style={{
							right: "10%",
							borderRadius: "20px",
						}}
						onClick={() => {
							dispatch({ type: ADD_TO_WISHLIST, payload: product });
						}}
					>
						<i className={`bi bi-heart${checkWishItem ? "-fill" : ""} h2`}></i>
					</button>

					<h1>{product?.name}</h1>

					<div className="d-flex align-items-center">
						<h2 className="pe-2">{product?.new_price}</h2>
						<small>TMT</small>
					</div>
					<div className="pt-2">
						<h5>
							{product?.category?.name}{" "}
							{product?.brand?.name ? `- ${product.brand.name}` : ""}
						</h5>
					</div>
					<div>
						<span className="text-success d-inline-flex align-items-center">
							<i class="bi bi-check-circle-fill me-2"></i>
							{t("product:availble")}{" "}
							<span style={{ fontWeight: "bold" }} className=" ms-1">
								{product?.amount}{" "}
							</span>{" "}
						</span>
						<span className="text-danger ms-2  d-inline-flex align-items-center">
							<i class="bi bi-bag-check-fill ms-2  me-1"></i>
							{t("product:sold")}{" "}
							<span style={{ fontWeight: "bold" }} className=" ms-1">
								{product?.sold}
							</span>{" "}
						</span>
					</div>
					{sizes.length < 1 && (
						<div className="h3 text-center ">
							<img
								className="img-fluid"
								src="https://error404.fun/img/illustrations/30.png"
							/>
							{t("product:Sorry, product is not available")}
						</div>
					)}
					{sizes.length >= 1 && (
						<p className="pt-3 truncate-overflow-3">{product?.description}</p>
					)}
					{sizes.length >= 1 && (
						<>
							<div className="h3">{t("product:Sizes")}:</div>
							<div className="row row-cols-auto justify-between align-items-center my-2 mx-auto">
								{sizes.map((size) => {
									const found = items.find((item) => item?.size.id === size.id);
									return (
										<div class="dropdown" key={size?.id}>
											<span
												id={size?.id}
												data-bs-toggle="dropdown"
												data-bs-auto-close="outside"
												aria-expanded="false"
												className="btn p-2 px-3 btn-outline-primary drpdw-none dropdown-toggle border rounded-pill  me-2 my-1 subcat-badge"
												role="button"
												style={{
													padding: "0.50rem 0.80rem",
													fontSize: "16px",
													fontWeight: "500",
												}}
											>
												{size?.size_name}
											</span>
											<ul
												class="dropdown-menu p-3 border-0"
												aria-labelledby={size?.id}
												style={{
													borderRadius: "15px",
													boxShadow: "0px 0px 30px 10px rgba(68, 55, 77, 0.2)",
												}}
											>
												<li>
													<span className="text-muted me-2">
														{t("product:availble")}
													</span>{" "}
													<strong>{size?.amount}</strong>
												</li>
												<li>
													{found ? (
														<div
															class="btn-group mx-auto my-3 col"
															role="group"
															aria-label="Basic example"
														>
															<button
																type="button "
																style={{
																	borderTopLeftRadius: "15px",
																	borderBottomLeftRadius: "15px",
																}}
																class="btn btn-primary"
																onClick={() =>
																	dispatch(reduceCartItem(size, product))
																}
															>
																<i class="bi bi-dash-lg"></i>
															</button>

															<button
																type="button"
																class="btn btn-primary "
																style={{ fontSize: "1.2rem !important" }}
															>
																{found?.quantity}
															</button>
															<button
																type="button"
																class="btn btn-primary"
																style={{
																	fontSize: "20px !important",
																	borderTopRightRadius: "15px",
																	borderBottomRightRadius: "15px",
																}}
																onClick={() => {
																	dispatch(addProduct(size, product));
																}}
															>
																<i class="bi bi-plus-lg"></i>
															</button>
														</div>
													) : (
														<button
															className="btn col-auto ps-3 my-3  btn-primary"
															style={{
																width: "100%",
																borderRadius: "20px",
															}}
															onClick={() => {
																dispatch(addProduct(size, product));
															}}
														>
															{t("product:addCart")}{" "}
														</button>
													)}
												</li>
											</ul>
										</div>
									);
								})}
							</div>
						</>
					)}
				</div>
			</div>

			{/* Product Info End */}
			{product && (
				<>
					<Tabs product={product} />
					<Recommendation product={product} />
				</>
			)}
		</div>
	);
}

export default Product;
