import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { get_recommendations } from "../../redux/actions/products";
import ProductSlider from "../Slider/ProductSlider";

function Recommendation({ product }) {
	const { t, i18n } = useTranslation(["product"]);
	const [retry, setRetry] = useState(0);
	const [products, setProducts] = useState([]);
	const dispatch = useDispatch();
	useEffect(() => {
		dispatch(get_recommendations(product?.id, i18n.language)).then((res) => {
			if (Array.isArray(res?.data) && res?.data.length >= 1) {
				setProducts(res?.data);
			} else {
				if (retry < 3) {
					setTimeout(() => {
						setRetry(retry + 1);
					}, retry * 2000);
				}
			}
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [product, retry, i18n.language]);
	return (
		<div className="row">
			<header className="section-heading d-flex justify-content-between  my-4">
				<h3 className="section-title ">{t("product:rec_for_you")}</h3>
			</header>
			<ProductSlider gotProducts={products} />
		</div>
	);
}

export default Recommendation;
